.App {
  text-align: center;
  font-family: 'Abel', sans-serif !important;

}

h1, h2, h3, h4, h5, p {
  font-family: 'Abel', sans-serif !important;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.landing-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.landing-wrapper .image-wrapper {
  margin-top: 5%;
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}

.button {
  border-radius: 25px
}

.card-wrapper {
  height: 400px;
  margin-top: 3% !important;
}

.main-wrapper{
  background-image: url('./assets/logo4.jpg') !important;
  background-size: 100% auto !important;
  -webkit-filter: blur(5px);
  -moz-filter: blur(5px);
  -o-filter: blur(5px);
  -ms-filter: blur(5px);
  filter: blur(5px);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.pusher{
}

.pusher-card{
  width: calc(71vw - 0px) !important;
  overflow: auto;
  box-shadow: 0 0px 0px 0 #d4d4d5, 0 0 0 0px #d4d4d5 !important;
}

